import React, { useState, useEffect } from "react";
import PropTypes from 'prop-types';

import { shuffleArray } from '../../../funcs.js';

import "./style.css";
import { Transition } from "react-transition-group";

// function roundWindowConstructor(roundMode, question, otherData, animationClassName, answers) { // otherData -- object with handlers

export default function RoundSelect (props) {
    const [buttonActive, setButtonActive] = useState(false);
    const [exitMode, setExitMode] = useState("RoundWrite");
    const [show, setShow] = useState(true);
    const [answers, setAnswers] = useState(props.answer);
    // console.log(answers);
    // console.log("props answers", props.answer);

    console.log("props", props);

    let answersHrefs = [];

    useEffect(()=>{
        setShow(true);
        setAnswers(shuffleArray(props.answer));
    }, [props.answer]);

    const roundClear = () => {
        setButtonActive(false);
        answersHrefs.forEach(ans => ans.current.checked = false);
    };

    const checkAnswer = (e) => {
        e.preventDefault();

        if (props.answer[0] === getAnswer()) {
            props.changeScore(1);
            setExitMode("right-answer");
        } else {
            props.changeScore(-1);
            setExitMode("fail-answer");
        }

        setShow(false);
    };

    const getAnswer = () => {
        let answerChecked = answersHrefs.filter(ans => ans.current.checked)[0];

        if (answerChecked) {
            return answerChecked.current.value
        } else {
            return ''
        }
    };

    const buttonActivate = () => {
        setButtonActive(true);
    };

    const text = props.text;
    const buttonText = buttonActive? "Проверить": "Пропустить";
    const buttonClassName = buttonActive?
        "RoundWindow__button RoundWindow__button_action":
        "RoundWindow__button";

    answers.forEach(() => {
        answersHrefs.push(React.createRef());
    });

    return (
        <Transition
            in={show}
            timeout={0}
            onExited={
                () => {
                    roundClear();
                    setTimeout(()=>{
                        setExitMode("RoundWrite");
                        setShow(true);
                        props.goNextRound(props.roundId);
                    },500);
                }
            }
        >
            {(state) => {
                const animationClassName = `${exitMode}-${state}`;
                return (
                    <div className={`RoundWindow ${animationClassName}`} key={props.roundId}>
                        <p className={`RoundWindow__text`} >{`${text}`}</p>
                        <form action="#" onSubmit={checkAnswer}>
                            {
                                answers.map((ans,ansId) => {
                                    return (
                                        <div
                                            className = "answer-select_block"
                                            key = {`${ans}-${ansId}`}
                                        >
                                            < input
                                                ref={answersHrefs[ansId]}
                                                id = {`answer-${ans}`}
                                                name = "possible_answer"
                                                type = "radio"
                                                value = {ans}
                                                onChange={buttonActivate}
                                            />
                                            <label htmlFor={`answer-${ans}`}>{ans}</label>
                                        </div>
                                    );
                                })
                            }
                            <input
                                type="submit"
                                value={buttonText}
                                className={buttonClassName}
                            />
                        </form>
                    </div>
                )
            }}
        </Transition>
    )
}

RoundSelect.propTypes = {
    text: PropTypes.string,
    answer: PropTypes.array,
    roundId: PropTypes.number,
    goNextRound: PropTypes.func,
    changeScore: PropTypes.func
};
